import React from 'react';
import { useTrail, animated } from 'react-spring';
import { Link } from 'react-router-dom';
import newLinkData, { defaultTrail } from '../../utils/LinkData';
import './style.css';

export default () => {
	const links = newLinkData();
	links.push('/general/interests', 'What makes you human?');
	links.push('/web/interests', 'What sparks your interest in web development?');
	links.push('/web/projects', 'What are your web development accomplishments?');
	links.push('/web/future', 'What are your future plans in web development?');
	links.push('/web/learn', 'What can you teach me about web development?');
	links.push('/game/about', 'What can you tell me about your past in game development?');
	links.push('/general/learn', 'Where can I learn more about you?');
	const trail = useTrail(links.data.length, defaultTrail);

	return (
		<>
			<p>What would you like to know about me?</p>
			<nav>
				<ul id="nav-menu">
					{
						trail.map((props, index) => (
							<animated.li key={ links.data[index].key } style={ props }><Link to={ links.data[index].link }>{ links.data[index].text }</Link></animated.li>
						))
					}
				</ul>
			</nav>
		</>
	);
};