type LinkData = {
	key: number,
	link: string,
	text: string,
}

const generateNewLinkData = () => {
	const newData:LinkData[] = [];
	let dataCounter = 0;
	return {
		data: newData,
		push: (link:string, text:string):void => {
			newData.push({
				key: dataCounter++,
				link: link,
				text: text,
			})
		},
	};
};

const defaultTrail = {
	opacity: 1,
	marginTop: 0,
	from: {
		opacity: 0,
		marginTop: 50,
	},
};

export { generateNewLinkData as default, defaultTrail }; 